
.theme-menu {
    position: fixed;
    /* right: -300px; */
    top: 0;
    width: 300px;
    height: 100vh;
    padding: 20px;
    z-index: 99;
    background-color: var(--main-bg);
    box-shadow: var(--box-shadow);
    transition: right 0.5s var(--transition-cubic);
}

.theme-menu.active {
    right: 0;
}

.theme-menu__close {
    position: absolute;
    top: 17px;
    right: 20px;
    background-color: transparent;
    font-size: 1.5rem;
}

.theme-menu__select {
    margin-top: 40px;
   
}

.mode-list {
    margin-top: 20px;
}

.mode-list > li {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.mode-list > li ~ li {
    margin-top: 10px;
}

.mode-list__color {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    font-size: 1.5rem;
}
.mode-list__color span{
    color: var(--txt-color);
}
.mode-list__color >svg {
    color: var(--txt-color);
    transform: scale(0);
    transition: transform 0.5s var(--transition-cubic);
}

.mode-list__color.active >svg {
    transform: scale(1);
}
div.MuiBox-root{
    background-color: var(--main-bg);
}